'use strict';

// MODULES //

import format from '@stdlib/string/format';
import INT8 from '@stdlib/constants/int8/num-bytes';
import UINT8 from '@stdlib/constants/uint8/num-bytes';
import INT16 from '@stdlib/constants/int16/num-bytes';
import UINT16 from '@stdlib/constants/uint16/num-bytes';
import INT32 from '@stdlib/constants/int32/num-bytes';
import UINT32 from '@stdlib/constants/uint32/num-bytes';
import FLOAT16 from '@stdlib/constants/float16/num-bytes';
import FLOAT32 from '@stdlib/constants/float32/num-bytes';
import FLOAT64 from '@stdlib/constants/float64/num-bytes';
import COMPLEX64 from '@stdlib/constants/complex64/num-bytes';
import COMPLEX128 from '@stdlib/constants/complex128/num-bytes';


// MAIN //

/**
* Returns the size (in bytes) of the canonical binary representation of a specified numeric type.
*
* @param {string} dtype - numeric type
* @throws {TypeError} must provide a recognized numeric type
* @returns {integer} size in bytes
*
* @example
* var s = sizeOf( 'int8' );
* // returns 1
*
* @example
* var s = sizeOf( 'uint8' );
* // returns 1
*
* @example
* var s = sizeOf( 'int16' );
* // returns 2
*/
function sizeOf( dtype ) {
	switch ( dtype ) {
	case 'float64':
		return FLOAT64;
	case 'int32':
		return INT32;
	case 'uint32':
		return UINT32;
	case 'float32':
		return FLOAT32;
	case 'int8':
		return INT8;
	case 'uint8':
		// Fall-through...
	case 'uint8c': // eslint-disable-line no-fallthrough
		return UINT8;
	case 'int16':
		return INT16;
	case 'uint16':
		return UINT16;
	case 'float16':
		return FLOAT16;
	case 'complex128':
		return COMPLEX128;
	case 'complex64':
		return COMPLEX64;
	default:
		throw new TypeError( format( 'invalid argument. Must provide a recognized type. Value: `%s`.', dtype ) );
	}
}


// EXPORTS //

export default sizeOf;
