/* eslint-disable stdlib/jsdoc-doctest-decimal-point */

'use strict';

// MODULES //

import format from '@stdlib/string/format';
import INT8 from '@stdlib/constants/int8/max';
import UINT8 from '@stdlib/constants/uint8/max';
import INT16 from '@stdlib/constants/int16/max';
import UINT16 from '@stdlib/constants/uint16/max';
import INT32 from '@stdlib/constants/int32/max';
import UINT32 from '@stdlib/constants/uint32/max';
import FLOAT16 from '@stdlib/constants/float16/pinf';
import FLOAT32 from '@stdlib/constants/float32/pinf';
import FLOAT64 from '@stdlib/constants/float64/pinf';


// MAIN //

/**
* Returns the maximum value of a specified numeric type.
*
* @param {string} dtype - numeric type
* @throws {TypeError} must provide a recognized numeric type
* @returns {number} maximum value
*
* @example
* var m = typemax( 'int8' );
* // returns 127
*
* @example
* var m = typemax( 'uint8' );
* // returns 255
*
* @example
* var m = typemax( 'int16' );
* // returns 32767
*/
function typemax( dtype ) {
	switch ( dtype ) {
	case 'float64':
		return FLOAT64;
	case 'int32':
		return INT32;
	case 'uint32':
		return UINT32;
	case 'float32':
		return FLOAT32;
	case 'int8':
		return INT8;
	case 'uint8':
		// Fall-through...
	case 'uint8c': // eslint-disable-line no-fallthrough
		return UINT8;
	case 'int16':
		return INT16;
	case 'uint16':
		return UINT16;
	case 'float16':
		return FLOAT16;
	default:
		throw new TypeError( format( 'invalid argument. Must provide a recognized type. Value: `%s`.', dtype ) );
	}
}


// EXPORTS //

export default typemax;
